@import "../../../variables";

.slider {
	width: 50%;
	position: relative;
	background: $GREEN; }
.slider__item {
	width: 100%;
	height: 100vh;
	overflow: hidden;
	position: relative;
	&:before {
		position: absolute;
		display: block;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		background: linear-gradient(to bottom, rgba(0,0,0, 0.1), #000); }
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center; } }
.slide__description {
	position: absolute;
	top: 30%;
	left: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: #ffffff;
	padding: 0 120px;
	h2 {
		font-size: 40px;
		font-weight: 700; }
	>span {
		padding: 50px 0;
		font-weight: 300;
		font-size: 24px;
		line-height: 90%;
		letter-spacing: 1px; }
	button {
		align-self: flex-start;
		@include mainbtn ($GREEN, #039F67, 30px); } }

.slider__prev, .slider__next {
	color: #fff;
	font-size: 30px;
	position: absolute;
	display: block;
	z-index: 1000;
	cursor: pointer;
	height: 100%;
	padding: 0 20px;
	display: flex;
	align-items: center;
	background: transparrent;
	transition: .3s linear;
	span {
		font-size: 50px; }
	&:hover {
		background: rgba(14, 194, 97, 0.2); } }
.slider__prev {
	left: 0; }
.slider__next {
	right: 0; }

// Slider dots
.slider__dots {
	position: absolute;
	z-index: 1000;
	margin: 0 auto;
	bottom: 20px;
	left: 50%;
	transform: translate(-50%, -50%);

	.slider-dots__item {
		display: inline-block;
		border-radius: 50%;
		width: 8px;
		height: 8px;
		background: #ffffff;
		margin: 4px;
		cursor: pointer; }
	.active, .slider-dots__item:hover {
		background-color: $GREEN; } }
